import React, { useEffect } from 'react';
import Link from 'next/link';
import PropTypes from 'prop-types';
import { Menu, Input, Button, Row, Col } from 'antd';
import { useSelector, useDispatch } from 'react-redux';
import Router from 'next/router';

import LoginForm from '../containers/LoginForm';
import UserProfile from '../containers/UserProfile';
import { LOAD_USER_REQUEST } from '../reducers/user';

const AppLayout = ({ children }) => {

  const { me } = useSelector(state => state.user);
  const dispatch = useDispatch();

  const onSearch = (value) => {
    /*    */
    Router.push({ pathname: '/hashtag', query: { tag: value } }, `hashtag/${value}`);   //  pathname: '/hashtag', query: { tag: value } }  
                                                                                        //  `hashtag/${value}` .
  };

  /* useEffect(() => {
    if (!me) {  //    
      dispatch({
        type: LOAD_USER_REQUEST,  //    (  .. -> sagas/user.js loadUserAPI()  )
                                  //   ,   LOAD_USER_REQUEST  
      });
    }
  }, []); */ 

  return (
    <div>
      <Menu mode="horizontal">
        <Menu.Item key="home"><Link href="/"><a></a></Link></Menu.Item>
        <Menu.Item key="profile"><Link href="/profile"><a></a></Link></Menu.Item>
        <Menu.Item key="mail">
          <Input.Search 
            enterButton 
            style={{ verticalAlign: 'middle' }}
            onSearch={onSearch}
          />
        </Menu.Item>
      </Menu>
      {!me && <Link href="/signup"><a><Button></Button></a></Link>}
      <Row gutter={10}>   {/* gutter={10} : xs, sm  N      */}
        <Col xs={24} md={6}>
          {me
            ? <UserProfile />
            :
            <LoginForm />
          }
        </Col>
        <Col xs={24} md={12}>
           {children}
        </Col>
        <Col xs={24} md={6}>

        </Col>
      </Row>
      
    </div>
  );
};

AppLayout.propTypes = {
  children: PropTypes.elementType,
};

export default AppLayout;