import React from 'react';
import Head from 'next/head';
import PropTypes from 'prop-types';
import withRedux from 'next-redux-wrapper';
import withReduxSaga from 'next-redux-saga';
import { createStore, compose, applyMiddleware } from 'redux';
import { Provider } from 'react-redux';
import createSagaMiddleware from 'redux-saga';
import axios from 'axios';
import Helmet from 'react-helmet';
import App, { Container } from 'next/app';

import AppLayout from '../components/AppLayout';
import rootSaga from '../sagas';
import reducer from '../reducers';  // ../reducers/index.js  (index.js  )
import { LOAD_USER_REQUEST } from '../reducers/user';
                                    // rootReducer reducer  (    )

const NodeBird = ({ Component, store, pageProps }) => {
    return (
        <Container>
            <Provider store={store}>
                <Helmet
                    title="NodeBird"
                    htmlAttributes={{ lang: 'ko' }}
                    meta={[{
                        charset: 'UTF-8',
                    }, {
                        name: 'viewport',
                        content: 'width=device-width,initial-scale=1.0,minimum-scale=1.0,maximum-scale=1.0,user-scalable=yes,viewport-fit=cover',
                    }, {
                        'http-equiv': 'X-UA-Compatible', content: 'IE=edge',
                    }, {
                        name: 'description', content: 'NodeBird SNS',
                    }, {
                        name: 'og:title', content: 'NodeBird',
                    }, {
                        name: 'og:description', content: 'NodeBird SNS',
                    }, {
                        property: 'og:type', content: 'website',
                    }]}
                    link={[{
                        rel: 'shortcut icon', href: '/linux.ico',
                    }, {
                        rel: 'stylesheet', href: 'https://cdnjs.cloudflare.com/ajax/libs/antd/3.16.2/antd.css',
                    }, {
                        rel: 'stylesheet', href: 'https://cdnjs.cloudflare.com/ajax/libs/slick-carousel/1.6.0/slick.min.css',
                    }, {
                        rel: 'stylesheet', href: 'https://cdnjs.cloudflare.com/ajax/libs/slick-carousel/1.6.0/slick-theme.min.css',
                    }]}
                />
                <AppLayout>
                    <Component {...pageProps}/>
                </AppLayout>
            </Provider>
        </Container>
        
    );
};

NodeBird.propTypes = {
    Component: PropTypes.elementType.isRequired, // isRequired   props   
    store: PropTypes.object.isRequired,
    pageProps: PropTypes.object.isRequired,
};

/* NodeBird.getInitialProps = async (context) => {
    //console.log(context);
    const { ctx } = context;
    let pageProps = {};
    if (context.Component.getInitialProps) {
        pageProps = await context.Component.getInitialProps(ctx);
    } 
    return { pageProps };
}; */

NodeBird.getInitialProps = async (context) => {
    //console.log(context);
    const { ctx, Component } = context;
    let pageProps = {};

    /* SSR,   cookie  . */
    const cookie = ctx.isServer ? ctx.req.headers.cookie : '';    // , ctx.req.headers cookie  .
    /*  , cookie     */
    if (ctx.isServer && cookie) {
        axios.defaults.headers.Cookie = cookie;     // axios cookie   . (SSR (=)  , ''->     )
                                                    // axios.defaults   axios  . 
    }
    /*     , */
    const state = ctx.store.getState();
    if (!state.user.me) {
        ctx.store.dispatch({
            type: LOAD_USER_REQUEST,
        });
    }

    /*   . */
    if (Component.getInitialProps) {                        // Component getInitialProps     
        pageProps = await Component.getInitialProps(ctx) || {};   // _app.js (context.ctx)   (context) . 
    } 
    
    return { pageProps };
};

/*  const NodeBird store    ->     ! */
const configureStore = (initialState, options) => {   // initialState , redux .
    const sagaMiddleware = createSagaMiddleware(); // redux-saga     .
    
    /*      middleware  ex)Redux Dev Tools */
    const middlewares = [sagaMiddleware]; 
    /* enhancer : middleware (apply),    */
    const enhancer = process.env.NODE_ENV === 'production'
        ? compose(applyMiddleware(...middlewares)) //   REDUX_DEVTOOLS  X
        : compose(   /* compose :  ,   */
        applyMiddleware(...middlewares),
        !options.isServer && window.__REDUX_DEVTOOLS_EXTENSION__ !== 'undefined' ? window.__REDUX_DEVTOOLS_EXTENSION__() : (f) => f,
    );                  
    /* store = reducer + state */                               
    const store = createStore(reducer, initialState, enhancer);   
    //sagaMiddleware.run(rootSaga);   // rootSaga sagaMiddleware  
    store.sagaTask = sagaMiddleware.run(rootSaga);   // [SSR]store.sagaTask withReduxSaga  . 
    return store;
};

export default withRedux(configureStore)(withReduxSaga(NodeBird));