import { all, fork, takeEvery, call, put, delay } from 'redux-saga/effects';
import axios from 'axios';
import { 
    LOAD_USER_FAILURE,
    LOAD_USER_REQUEST,
    LOAD_USER_SUCCESS,
    LOG_IN_FAILURE,
    LOG_IN_REQUEST,
    LOG_IN_SUCCESS,
    LOG_OUT_FAILURE,
    LOG_OUT_REQUEST,
    LOG_OUT_SUCCESS,
    SIGN_UP_FAILURE,
    SIGN_UP_REQUEST,
    SIGN_UP_SUCCESS,
    FOLLOW_USER_FAILURE,
    FOLLOW_USER_REQUEST,
    FOLLOW_USER_SUCCESS,
    UNFOLLOW_USER_FAILURE,
    UNFOLLOW_USER_REQUEST,
    UNFOLLOW_USER_SUCCESS,
    LOAD_FOLLOWERS_FAILURE,
    LOAD_FOLLOWERS_REQUEST,
    LOAD_FOLLOWERS_SUCCESS,
    LOAD_FOLLOWINGS_FAILURE, 
    LOAD_FOLLOWINGS_REQUEST,
    LOAD_FOLLOWINGS_SUCCESS,
    REMOVE_FOLLOWER_FAILURE,
    REMOVE_FOLLOWER_REQUEST,
    REMOVE_FOLLOWER_SUCCESS,
    EDIT_NICKNAME_FAILURE, 
    EDIT_NICKNAME_REQUEST,
    EDIT_NICKNAME_SUCCESS
} from '../reducers/user';

/**
 *  3
 */
function loginAPI(loginData) {   //     
    return axios.post('/user/login', loginData, {
        withCredentials: true,
    }); 
}
function* login(action) {
    try {
        const result = yield call(loginAPI, action.data); //  
        yield put({     // put dispatch  
            type: LOG_IN_SUCCESS,
            data: result.data, // axios(result) .data . 
        });
    } catch (e) {
        console.error(e);
        yield put({
            type: LOG_IN_FAILURE,
        });
    }
}
function* watchLogin() {
    yield takeEvery(LOG_IN_REQUEST, login);
}

/**
 *  3
 */
function signUpAPI(signUpData) {
    //     
    return axios.post('/user', signUpData); //  signUpData req.body .
}
function* signUp(action) {
    try {
        yield call(signUpAPI, action.data);
        yield put({
            type: SIGN_UP_SUCCESS,
        });
    } catch (e) {
        console.error(e);
        yield put({
            type: SIGN_UP_FAILURE,
            error: e,
        });
    }
}
function* watchSignUp() {
    yield takeEvery(SIGN_UP_REQUEST, signUp);
}

/**
 *  3
 */
function logOutAPI() {
    //    
    return axios.post('/user/logout', {}, {
      withCredentials: true,
    });
}
function* logOut() {
    try {
      yield call(logOutAPI);
      yield put({ // put dispatch 
        type: LOG_OUT_SUCCESS,
      });
    } catch (e) {
      console.error(e);
      yield put({
        type: LOG_OUT_FAILURE,
        error: e,
      });
    }
}
function* watchLogOut() {
    yield takeEvery(LOG_OUT_REQUEST, logOut);
}
  
/**
 *     3
 */
function loadUserAPI(userId) {
    //    
    return axios.get(userId ? `/user/${userId}` : '/user/', {   // userId   
                                                                //       
      withCredentials: true,    //   
    });
}
function* loadUser(action) {
    try {
      const result = yield call(loadUserAPI, action.data);
      yield put({ // put dispatch 
        type: LOAD_USER_SUCCESS,
        data: result.data,
        isThisMe: !action.data,   //   LOAD_USER_REQUEST id  .
                            //  ( ) 
                            // -> action.data === false (=  false)
                            // -> !action.data === true
                            // -> action.isThisMe===true LOAD_USER_SUCCESS .
      });
    } catch (e) {
      console.error(e);
      yield put({
        type: LOAD_USER_FAILURE,
        error: e,
      });
    }
}
function* watchLoadUser() {
    yield takeEvery(LOAD_USER_REQUEST, loadUser);
}

/**
 *   3
 */
function followAPI(userId) {
    //    
    return axios.post(`/user/${userId}/follow`, {}, {
      withCredentials: true,
    });
}
function* follow(action) {
    try {
      const result = yield call(followAPI, action.data);
      yield put({ // put dispatch 
        type: FOLLOW_USER_SUCCESS,
        data: result.data,
      });
    } catch (e) {
      console.error(e);
      yield put({
        type: FOLLOW_USER_FAILURE,
        error: e,
      });
    }
}
function* watchFollow() {
    yield takeEvery(FOLLOW_USER_REQUEST, follow);
}
  
/**
 *   3
 */
function unfollowAPI(userId) {
    //    
    return axios.delete(`/user/${userId}/follow`, {     // delete data:{}  X
      withCredentials: true,
    });
}
function* unfollow(action) {
    try {
      const result = yield call(unfollowAPI, action.data);
      yield put({ // put dispatch 
        type: UNFOLLOW_USER_SUCCESS,
        data: result.data,
      });
    } catch (e) { 
      console.error(e);
      yield put({
        type: UNFOLLOW_USER_FAILURE,
        error: e,
      });
    }
}
function* watchUnfollow() {
    yield takeEvery(UNFOLLOW_USER_REQUEST, unfollow);
}

/**
 *     3
 */
function loadFollowersAPI(userId, offset = 0, limit = 3) {  //   
  //    
  return axios.get(`/user/${userId || 0}/followers?offset=${offset}&limit=${limit}`, {
    withCredentials: true,
  });
}
function* loadFollowers(action) {
  try {
    const result = yield call(loadFollowersAPI, action.data, action.offset);  //   action.offset  X
                                                                              //    action.offset .
    yield put({ // put dispatch 
      type: LOAD_FOLLOWERS_SUCCESS,
      data: result.data,
    });
  } catch (e) { 
    console.error(e);
    yield put({
      type: LOAD_FOLLOWERS_FAILURE,
      error: e,
    });
  }
}
function* watchLoadFollowers() {
  yield takeEvery(LOAD_FOLLOWERS_REQUEST, loadFollowers);
}

/**
 *       3
 */
function loadFollowingsAPI(userId, offset = 0, limit = 3) {
  //    
  return axios.get(`/user/${userId || 0}/followings?offset=${offset}&limit=${limit}`, {
    withCredentials: true,
  });
}
function* loadFollowings(action) {
  try {
    const result = yield call(loadFollowingsAPI, action.data, action.offset);
    yield put({ // put dispatch 
      type: LOAD_FOLLOWINGS_SUCCESS,
      data: result.data,
    });
  } catch (e) {
    console.error(e);
    yield put({
      type: LOAD_FOLLOWINGS_FAILURE,
      error: e,
    });
  }
}
function* watchLoadFollowings() {
  yield takeEvery(LOAD_FOLLOWINGS_REQUEST, loadFollowings);
}

/**
 *    3
 */
function removeFollowerAPI(userId) {
  //    
  return axios.delete(`/user/${userId}/follower`, {
    withCredentials: true,
  });
}
function* removeFollower(action) {
  try {
    const result = yield call(removeFollowerAPI, action.data);
    yield put({ // put dispatch 
      type: REMOVE_FOLLOWER_SUCCESS,
      data: result.data,
    });
  } catch (e) { 
    console.error(e);
    yield put({
      type: REMOVE_FOLLOWER_FAILURE,
      error: e,
    });
  }
}
function* watchRemoveFollower() {
  yield takeEvery(REMOVE_FOLLOWER_REQUEST, removeFollower);
}

/**
 *   3
 */
function editNicknameAPI(nickname) {
  //    
  return axios.patch('/user/nickname', { nickname }, {  //  : patch,  : put
    withCredentials: true,
  });
}
function* editNickname(action) {
  try {
    const result = yield call(editNicknameAPI, action.data);
    yield put({ // put dispatch 
      type: EDIT_NICKNAME_SUCCESS,
      data: result.data,
    });
  } catch (e) { 
    console.error(e);
    yield put({
      type: EDIT_NICKNAME_FAILURE,
      error: e,
    });
  }
}
function* watchEditNickname() {
  yield takeEvery(EDIT_NICKNAME_REQUEST, editNickname);
}

/*  main */
function* userSaga() {
    yield all([
        fork(watchLogin),
        fork(watchLogOut),
        fork(watchLoadUser),
        fork(watchSignUp),
        fork(watchFollow),
        fork(watchUnfollow),
        fork(watchLoadFollowers),
        fork(watchLoadFollowings),
        fork(watchRemoveFollower),
        fork(watchEditNickname),
    ]);
}
export default userSaga;