import { all, fork } from 'redux-saga/effects';
import axios from 'axios';
import user from './user';
import post from './post';

// axios.post  URL      
axios.defaults.baseURL = 'http://localhost:3067/api';       // axios.defaults   axios  . 

function* rootSaga() {
    yield all([
        fork(user),
        fork(post),
    ]);
}

export default rootSaga;